// frontend/src/components/layout/ThemeToggleFinal.tsx
'use client';

import { useState, useEffect } from 'react';
import { Sun, Moon } from 'lucide-react';

export default function ThemeToggleFinal() {
  const [isDark, setIsDark] = useState(false);
  const [mounted, setMounted] = useState(false);

  // Initialisation
  useEffect(() => {
    console.log('=== THEME INIT ===');
    
    // VÃ©rifier localStorage
    const savedTheme = localStorage.getItem('vibecro-theme');
    console.log('Saved theme:', savedTheme);
    
    // VÃ©rifier la prÃ©fÃ©rence systÃ¨me
    const systemPrefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    console.log('System prefers dark:', systemPrefersDark);
    
    // DÃ©terminer le thÃ¨me
    let initialIsDark = false;
    
    if (savedTheme === 'dark') {
      initialIsDark = true;
    } else if (savedTheme === 'light') {
      initialIsDark = false;
    } else if (systemPrefersDark) {
      initialIsDark = true;
    }
    
    console.log('Initial isDark:', initialIsDark);
    setIsDark(initialIsDark);
    
    // Appliquer le thÃ¨me IMMÃ‰DIATEMENT
    if (initialIsDark) {
      document.documentElement.classList.add('dark');
      console.log('âœ… Added dark class to html');
    } else {
      document.documentElement.classList.remove('dark');
      console.log('âœ… Removed dark class from html');
    }
    
    console.log('HTML classes:', document.documentElement.classList);
    console.log('=== END INIT ===');
    
    setMounted(true);
  }, []);

  const toggleTheme = () => {
    console.log('=== TOGGLE THEME ===');
    const newIsDark = !isDark;
    console.log('Changing from', isDark ? 'dark' : 'light', 'to', newIsDark ? 'dark' : 'light');
    
    setIsDark(newIsDark);
    
    // Sauvegarder
    localStorage.setItem('vibecro-theme', newIsDark ? 'dark' : 'light');
    
    // Appliquer
    if (newIsDark) {
      document.documentElement.classList.add('dark');
      console.log('âœ… Added dark class');
    } else {
      document.documentElement.classList.remove('dark');
      console.log('âœ… Removed dark class');
    }
    
    console.log('HTML classes after toggle:', document.documentElement.classList);
    console.log('=== END TOGGLE ===');
  };

  // Pendant le SSR
  if (!mounted) {
    return (
      <div className="w-12 h-12 rounded-full bg-gray-200 animate-pulse" />
    );
  }

  return (
    <button
      onClick={toggleTheme}
      className="relative w-12 h-12 rounded-full bg-gray-100 dark:bg-gray-800 
                 flex items-center justify-center shadow-lg hover:shadow-xl
                 border border-gray-200 dark:border-gray-700
                 transition-all duration-300 hover:scale-105"
      aria-label={isDark ? "Passer en mode clair" : "Passer en mode sombre"}
    >
      {isDark ? (
        <Sun className="w-5 h-5 text-yellow-500" />
      ) : (
        <Moon className="w-5 h-5 text-gray-700" />
      )}
      
      {/* Indicateur */}
      <span className="absolute -bottom-1 -right-1 w-3 h-3 bg-green-500 rounded-full border-2 border-white dark:border-gray-900" />
    </button>
  );
}